.Header {
    width: 90vw;
    background-color: #222;
    color: white;
    display: inline-flex;
    align-items: center;
    justify-content: space-around;
    box-shadow: 0px 0px 50px #000;
    border-radius: 30px;
    margin: 60px 0;
}

.image {
    height: 205px;
    width: 205px;
    border-radius: 100%;
    background: var(--degrade);
    /*Alinha a imagem dentro da div*/
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
}

.image img {
    border-radius: 100px;
}

.name .texto {
    margin-left: 10px;
    margin-top: 100px;
}

.name .texto h2 {
    color: #aaa;
    font-style: italic;
}

li {
    font-size: 18px;
    margin-top: 10px;
}

.name {
    display: flex;
    align-items: center;
}